html {
  font-size: 62.5%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fixed {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

.container {
  width: min(120rem, 90%);
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
  width: 100%;
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #fff;
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .scene-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.scene-container p {
  color: #000;
}

.scene {
  padding-top: 2rem;
}
.scene p:after {
  content: "";
  display: block;
  width: 20%;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.bg-yellow {
  background-color: #fef46d;
}
.bg-yellow li:nth-child(odd) {
  background: #fef03a;
}

.bg-green {
  background-color: #4cb8b3;
}
.bg-green li:nth-child(odd) {
  background: #3b9691;
}

.edm .name-scene:after {
  background-image: url(../img/icono_edm.png);
}

.rock .name-scene:after {
  background-image: url(../img/icono_rock.png);
}

.calendar {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendar li {
  font-weight: 700;
  padding: 2.6rem;
}
.calendar li span {
  font-weight: 400;
}

.header {
  background-color: #4cb8b3;
}
@media (min-width: 768px) {
  .header.fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    box-shadow: 0.3rem 0.3rem 0.5rem rgba(0, 0, 0, 0.3);
  }
}
.header h1 {
  color: #fff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .content-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .content-header {
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .main-nav {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
.main-nav a {
  display: block;
  text-align: center;
  color: #fff;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
.main-nav a:hover {
  color: #fef46d;
}
@media (min-width: 768px) {
  .main-nav a {
    margin: 0;
  }
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 0, 0.5) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.content-video {
  z-index: 10;
}
.content-video h2 {
  color: #fff;
  font-size: 7rem;
  margin: 0;
}
.content-video p {
  background-color: #f53756;
  display: inline-block;
  padding: 1rem 2rem;
  color: #fff;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.about-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .about-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.content-festival {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .content-festival {
    margin-top: 0;
  }
}
.content-festival h2 {
  margin-bottom: 2rem;
}
.content-festival .date {
  color: #f53756;
  font-weight: bold;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery-images {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 60rem;
}

.btn-close {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #fff;
  color: #000;
  display: grid;
  place-items: center;
  font-weight: 900;
}
.btn-close:hover {
  cursor: pointer;
}

.tickets {
  padding: 5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #fff;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .tickets .container-tickets {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase .name-pase {
  text-align: center;
  color: #fff;
  font-size: 2.4rem;
  font-weight: 700;
}
.pase-header, .pase-footer {
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #fff;
  margin: 0;
}
.pase-header {
  background-color: #f53756;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  background-color: #fff;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li,
.pase-body ul :nth-child(odd) {
  background-color: #fff;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #752F97;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #f53756;
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #fff;
}