@use 'variables' as v;

html {
  font-size: 62.5%;
}

body {
  font-family: v.$main-font;
  font-size: 1.6rem;
  &.fixed {
    overflow: hidden;
  }
  &.body-scroll {
    padding-top: 8.6rem;
  }
}

 p {
   font-size: 2rem;
   color: v.$black;
   line-height: 1.5;
 }

 .container  {
   width: min(120rem, 90%);
    margin: 0 auto;
 }
 a {
   text-decoration: none;
 }

 h1 , h2 , h3 {
   margin: 0 0 5rem 0;
 }

 h1 {
   font-size: 4rem;
 }

 h2 {
    font-size: 3.6rem;
 }

 h3 {
   font-size: 3rem;
 }

 img {
   max-width: 100%;
   display: block;
   height: auto;
   width: 100%;
 }